const sourceChar = '/'
const spaceChar = ' '

export const injectSourcetAt = (input: string, pos: number) => {
    let array = input.split('')
    let result = ''
    array.splice(pos, 0, sourceChar);
    result = array.join('')
    return result
}

export const injectSpacetAt = (input: string, pos: number) => {
    let array = input.split('')
    let result = ''
    array.splice(pos, 0, spaceChar);
    result = array.join('')
    return result
}

export const renderDate = (input: string) => {
    let array = input.split('');
    let result = '';
    if (input.length === 3) {
        if (input.indexOf('/') !== -1) {
            array.splice(2, 0, ' ');
            result = array.join('');
            return result;
        } else {
            array.splice(2, 0, ' / ');
            result = array.join('');
            return result;
        }
    }
    if (input.length === 4) {
        array.splice(3, 0, '/ ');
        result = array.join('');
        return result;
    }
    if (input.length === 5) {
        array.splice(4, 0, ' ');
        result = array.join('');
        return result;
    }
    if (input.length === 8) {
        if (input.substring(5).indexOf('/') !== -1) {
            array.splice(7, 0, ' ');
            result = array.join('');
            return result;
        } else {
            array.splice(7, 0, ' / ');
            result = array.join('');
            return result;
        }
    }
    if (input.length === 9) {
        if (input[7] === spaceChar) {
            array.splice(8, 0, '/ ');
            result = array.join('');
            return result;
        } else {
            result = array.join('');
            return result;
        }
    }
    if (input.length === 10) {
        result = array.join('');
        return result;
    }
    if (input.length === 11) {
        const isSpaceAtPos2 = input[2] !== spaceChar
        const isSpaceAtPos4 = input[4] !== spaceChar
        const isSpaceAtPos7 = input[7] !== spaceChar
        const isSpaceAtPos9 = input[9] !== spaceChar

        if (isSpaceAtPos2 && input[1] !== spaceChar) {
            return injectSpacetAt(input, 2)
        } else if (isSpaceAtPos4 && input[3] !== spaceChar) {
            return injectSpacetAt(input, 4)
        } else if (isSpaceAtPos7 && input[6] !== spaceChar) {
            return injectSpacetAt(input, 7)
        } else if (isSpaceAtPos9 && input[8] !== spaceChar) {
            return injectSpacetAt(input, 9)
        }
        const isSourceAtPos3 = input[3] !== sourceChar
        const isSourceAtPos8 = input[8] !== sourceChar

        if (isSourceAtPos3 && input[2] !== sourceChar) {
            return injectSourcetAt(input, 3)
        } else if (isSourceAtPos8 && input[7] !== sourceChar) {
            return injectSourcetAt(input, 8)
        }
        return input;
    }
}


export const renderDateMobile = (input: string) => {
    let yyyy = input.substr(0, 4);
    let mm = input.substr(5, 2);
    let dd = input.substr(8, 2);
    let result = dd + "/" + mm + "/" + yyyy;
    return result;
}
