import React, { useState, forwardRef, useEffect } from 'react'
import styled from 'styled-components'

import TextInput from '../TextInput/index'
import ValidationIndicator from '../ValidationIndicator'
import Tooltip from '../Tooltip'
import Label from '../Label'
import LabelIcon from '../Label/LabelIcon'
import useWindowDimensions from '../handleMobile'

export type Props = {
  onInputSuccess?: (wasAnInputSuccess: boolean) => void,
  nameProp: string,
  isValidated?: boolean,
  inputFromProps?: string,
  hasDateInputValueChange?: (hasChange: boolean) => void,
}

const DateInputOnDesktop = styled.div``

type Ref = HTMLInputElement

export const DateInput = forwardRef<Ref, Props>((props, aRef) => {

  const { nameProp, onInputSuccess, inputFromProps, 
    isValidated, hasDateInputValueChange } = props

  const [isSuccess, setIsSuccess] = useState(false);
  const [isVisible, setIsVisible] = useState(false);
  const [validatorMsg, setValidatorMsg] = useState("")
  const [labelIconState, setLabelIconStateState] = useState(true)
  const [hasInputChange, setInputChange] = useState(false)
  const [inputType, setInputType] = useState('text')

  const { width } = useWindowDimensions()

  useEffect(() => {
    onInputSuccess ? onInputSuccess(isSuccess) : {}
    if (isSuccess) {
      setValidatorMsg("")
    } else {
      setValidatorMsg("Enter in the format DD/MM/YY")
    }
  }, [isSuccess])
  
  useEffect(() => {
    if(isVisible === true && isSuccess === false) {
      setLabelIconStateState(false)
    } else {
      setLabelIconStateState(true)
    }
  },[isSuccess, isVisible])

  useEffect(() => {
    hasDateInputValueChange ? hasDateInputValueChange(hasInputChange) : {}
  },[hasInputChange])

  const DetectMobileRes = () => {
    if (width < 1024) {
      setInputType('date')
    }
  }

  useEffect(() => {

  },[width])

  return (
    <div>
      <Label>Date of birth <LabelIcon iconStates={labelIconState}>{'  '}*</LabelIcon></Label>
      <DateInputOnDesktop
        onClick={DetectMobileRes}
      >
        <TextInput
          placeholder="DD/MM/YY"
          name={nameProp + 'OnDesktop'} maxLength={12} type={inputType}
          onInputSucessCallback={success => {
            setIsSuccess(success)
          }}
          onVisibility={visible => setIsVisible(visible)}
          ref={aRef} 
          inputFromProps={inputFromProps}
          isValidated={isValidated}
          hasValueChange={isChange => {
            setInputChange(isChange)
          }}
        />
      </DateInputOnDesktop>
      <ValidationIndicator
        visibility={isVisible}
        isCorrect={isSuccess}
        message={validatorMsg}
      />
      <Tooltip visibility={isVisible} tooltipName="date"
        tooltipTitle="Why do we need this?"
        tooltip_message="We need to know how old you are so 
      we can ensure you are being paid correctly for your age.
      This is especially important for junior workers." />
    </div>
  )
})

export default DateInput;
